<script>
import { getStat } from "@/service/node";

export default {
    name: "DatabaseTabs",
    props: {
        client: {
            type: String,
            default: "std",
        },
        type: {
            type: String,
            default: "std",
        },
        hasRight: {
            type: Boolean,
            default: false,
        },
    },
    data: () => ({
        stat: {
            skill: 0,
            buff: 0,
            item: 0,
            npc: 0,
            doodad: 0,
        },
    }),
    model: {
        prop: "type",
        event: "change",
    },
    computed: {
        typeModel: {
            get() {
                return this.type;
            },
            set(val) {
                this.$emit("update:type", val);
            },
        },
    },
    methods: {
        changeType() {
            this.$emit("change", this.type);
        },
        loadStat() {
            getStat(this.client).then((res) => {
                const data = res.data;
                this.stat = data;
            });
        },
    },
    watch: {
        client: {
            handler: function () {
                this.loadStat(this.client);
            },
            immediate: true,
        },
    },
};
</script>
